import React from "react";
import { Link } from "gatsby";

interface ILayoutProps{
    children: any;
    title: string;
}

export default function Layout({children, title}: ILayoutProps){
    return(
        <div className="container">
            <nav>
                <ul>
                    <li>
                        <Link to="/">Blog</Link>
                    </li>
                    <li>
                        <Link to="/about">About</Link>
                    </li>
                </ul>
            </nav>
            <h1>{title}</h1>
            <main>{children}</main>
        </div>
    );
};